.dialog {
  position: relative;
  max-height: calc(100vh - var(--spacer-160));
  padding: var(--spacer-32);
  background-color: var(--color-grey-00);
  overflow-x: auto;
}

.title {
  composes: title4 from '~typo';
}

.description {
  composes: bodyLarge from '~typo';
  margin: var(--spacer-32) 0;
}

.table {
  composes: body from '~typo';
}

.row {
  border-bottom: 1px solid var(--color-grey-15);
}

.header {
  composes: eyebrow from '~typo';
  color: var(--color-cobalt);
}

.cell {
  padding: var(--spacer-12);
  text-align: left;

  &:first-child {
    text-align: center;
  }
}

.common {
  composes: label from '~typo';
  color: var(--color-grey-100);
}

.buttons {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  margin-top: var(--spacer-16);
  gap: var(--spacer-16);
}
