.banner {
  /* We compose the container to inherit from the font-size and adjust the width accordingly */
  composes: analyticsBanner from '~typo';
  position: fixed;
  z-index: 10;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: var(--spacer-32);
  border-top: 1px solid var(--color-grey-10);
  background-color: var(--color-grey-100);
  border-radius: var(--border-radius-20) var(--border-radius-20) 0 0;
  gap: var(--spacer-24);
  @media (--xs) {
    right: min(var(--spacer-64), 7vw);
    bottom: min(var(--spacer-64), 7vw);
    left: auto;
    width: 33em;
    border-radius: var(--border-radius-20);
  }
}

.buttons {
  display: flex;
  justify-content: flex-start;
  gap: var(--spacer-16);
}

/* Close button if no consent manager */
.closeButton {
  position: absolute;
  top: var(--spacer-16);
  right: var(--spacer-16);
  width: 0.9em;
  height: 0.9em;

  path {
    fill: var(--color-grey-00);
  }
}

.bodies {
  composes: analyticsBanner from '~typo';
  margin: 0;
  color: var(--color-grey-00);
  text-align: left;
}
